.home {
    background-image: var(--first-gradient);
    
    padding-bottom: 180px;
}
.home, .home__wrapper
.home__container{
    position: relative;
}
.home__wrapper{
    min-height: 100vh;
    display: grid;
    align-items: center;
}

.home__container {
    width: 100%;
}

.home__subtitle, .home__job{
    font-weight: 700;
}

.home__subtitle, .home__job span{
    font-size: var(--small-font-size);
    color: var(--title-color);
}

.home__subtitle span, .home__title span{
    color: var(--primary-color);
}

.home__job b{
    font-family: var(--second-font);
    font-size: var(--h2-font-size);
    margin-left: 10px;
}

.home__title{
    color: #fff;
    font-size: var(--biggest-font-size);
    text-shadow: 2px 2px hsl(0, 0%, 0%), -2px 2px hsl(0, 0%, 0%), 
        2px -2px hsl(0, 0%, 0%), -2px -2px hsl(0, 0%, 0%), 
        5px 5px 0 rgb(0 0 0/ 20%);
    list-style: 1.2;
    margin-top: 15px;
}

.home__text{
    font-size: var(--large-font-size);
    margin-block: 40px 28px;
    max-width: 520px;
}

.home__socials{
    display: flex;
    column-gap: 20px;
    margin-bottom: 30px;
}

.home__social-link{
    color: var(--title-color);
    font-size: var(--h5-font-size);
    transition: all 0.7s var(--transition);
}

.home__social-link:hover{
    color: var(--primary-color);
}

.home__btns{
    display: flex;
    column-gap: 70px;
}

.hero__link::before{
    content: '';
    position: absolute;
    top: 50%;
    left: -97%;
    transform: translateY(-50%);
    width:40px;
    height: 2px;
    background-color: var(--border-color);
}

.home__img-wrapper{
    position: absolute;
    right: 12px;
    top: 0;
    transform: translateY(10%);
    width: 100%;
    max-width: 600px;
}

.home__banner{
    border: 3px solid var(--primary-color);
    padding-bottom: 100%;
    border-radius: 20%;
    position: relative;
    z-index:1;
}

.home__profile {
    position: absolute;
    bottom: 0;
    height: 700px;
    padding-left: 200px;
    border-radius: 500px;
    object-fit: cover;
}

.home__data{
    border: 2px solid var(--border-color);
    background: var(--container-color);
    box-shadow: var(--shadow);
    padding-inline: 20px;
    width: 250px;
    height: 82px;
    border-radius: 82px;
    display: flex;
    align-items: center;
    position: absolute;
    z-index: 1;
}

.home__data-one{
    bottom: 20%;
    left: -4%;
}

.home__data-two{
    bottom: 12%;
    right: 6%;
}

.home__data .text-lg,
.home__data .text-sm{
    width: 50%;
    color: var(--title-color);
    font-weight: 700;
}

.home__data .text-lg{
    text-align: center;
    font-size: 37px;
}

.home__data .text-sm{
    font-size: 13px;
    line-height: 1.5em;
}

.home__data .text-lg b,
.home__data .text-sm span{
    color: var(--primary-color);
}

.home__data .text-lg b{
    position: relative;
    top: -4px;

}

.shape__1{
    width: 226px;
    height: 226px;
    top: -6%;
    right: -6%;
}

.shape__2,
.shape__3{
    width: 141px;
    height: 141px;
}

.shape__2{
    bottom: 8%;
    left: -16%;
}

.shape__3{
    bottom: 0;
    right: -2%;
}